.sudoku-board {
  -webkit-box-shadow: 0 0 25px 0 rgba(0,0,0,0.9);
  box-shadow: 0 0 25px 0 rgba(0,0,0,0.9);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 50vw;
  margin: auto;
  width: 50vw;
}

.timer-container {
  display: -ms-flexbox;
  display: flex;
}

.board-blocks {
  display: -ms-flexbox;
  display: flex;
  height: 50vw;
  position: fixed;
  width: 50vw;
  z-index: -1;
}

.board-blocks-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
}

.board-spaces {
  -ms-flex-align: center;
      align-items: center;
  caret-color: transparent;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
  z-index: 1;
}

.board-spaces .board-row {
  display: -ms-flexbox;
  display: flex;
  height: calc(100%/9);
  width: 100%;
}

.board-row div {
  -ms-flex-align: center;
      align-items: center;
  border: 1px solid black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  font-weight: bold;
  -ms-flex-pack: center;
      justify-content: center;
}

.board-block {
  border: 2px solid black;
  -ms-flex: 1 1;
      flex: 1 1;
}

[contenteditable="true"]:active,
[contenteditable="true"]:focus{
  background: #e0e0e0;
  border: 1px solid black;
  outline: none;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease;
}

.board-space.flash-red {
  background: red;
  -webkit-transition: all .3s linear;
  -o-transition: all .3s linear;
  transition: all .3s linear;
}



